
<script>
import list from './v.s.list.vue'
export default {
    name:"LIST-WRAPPABLE-DOWN",
    extends:list,
    computed:{}
}
</script>

<style lang="less">
/**
    --list-gap
    --list-gap-column
    --list-gap-row
    --list-item-basis
    --list-flow
*/
.LIST-WRAPPABLE-DOWN
{
    &.LIST.ROOT
    {
        --list-wrap:wrap;
        --list-gap:1em;
        --list-direction:row;
        padding:var(--list-gap,0px);
        box-sizing:border-box;
        display: inline-grid;
        align-items: center;
        justify-items: center;
        grid-template-rows: repeat(auto-fit, minmax(400px, 1fr));
        grid-template-columns: 1fr;
        justify-content: var(--list-justfied,center);
        grid-column-gap: var(--list-gap-column,var(--list-gap,0px));
        grid-row-gap: var(--list-gap-row,var(--list-gap,0px));
        width:auto;
        height: auto;
    }}
</style>



