<template>

    <down-list  class="" 
        :data-state="collapsed?'COLLAPSED':'OPEN'"
        tab-index="0"
        :style="navStyles()" 
        :data="data" 
        @click.native="toggleCollapseState"
        />
</template>
<script>
import downList from "./v.s.list.fluidDown.vue"
export default {
    name:"BURGER-LIST",
    components:{
        downList
    },
    props:["data"],
    methods:{
        toggleCollapseState()
        {
            this.collapsed=!this.collapsed;
        },
    },
    data()
    {
        return {
            collapsed:true,
            navStyles:()=>({})
        }
    },
    mounted() {
        this.navStyles=function()
        {
            
            return  {
                // '--menu-left':(this.$el.getClientRects()[0].right-(this.$el.offsetWidth)/2)+'px',
                // '--menu-top':(this.$el.getClientRects()[0].bottom-(this.$el.offsetHeight)/2)+'px',
            '--menu-right':'2em',
            '--menu-top':'50px'
            }
        }
    }
}
</script>
<style lang="less">
.BURGER-LIST
{

    background: url(../assets/wall/open.png);
    background-size: 2em 2em;
    background-position: center;
    background-repeat: no-repeat;
    & *
    {
        transition:none;
    }
    &[data-state="COLLAPSED"]
    {
         cursor: pointer;
        // filter: invert(1);
        // width:3.2em !important;
        // height:100px !important;
        // grid-template-rows: repeat(auto-fill, minmax(0em,1fr)) !important;
        // transform: scaleY(0.01) scaleX(0.07) ;
        grid-column-gap:10vh !important ;
        & > *
        {
            display: none;
        }
        // zoom: 0.7;
        // justify-self: center;
        // align-self: center;
        // .LIST-ITEM
        // {
        //     width:3em !important;
        //     height:0.5em !important; 
        // }
    }
    &[data-state="OPEN"]
    {
         width:var(--menu-width,auto) !important;
         height:var(--menu-height,auto) !important;
        position: absolute;
        top:var(--menu-top,unset);
        bottom:var(--menu-bottom,unset);
        left:var(--menu-left,unset);
        right:var(--menu-right,unset);
    }

}
</style>



